/**
 * Basic HTML element styles
 */
.hidden-label     { font-size:0; height:1px; overflow:hidden; }
.fa-times     { color:red;   }
.fa-pencil     { color:blue;  }
.fa-edit        { color:blue;  }
.fa-plus       { color:green; }
.fa-ok         { color:green; }
.fa-save       { color:green; }
.fa-map-marker   { color:blue;  }
.fa-list-ul    { color:blue;  }
.fa-print       { color:blue;  }

/**
 * Side Panel
 */
#panel-one nav {
  border:2px solid #000; margin-bottom:0.5em;
  border-radius:10px;
  overflow:hidden;
}
#panel-one h2 {
  background-color:#36c; color:#fff;
  border-bottom:2px solid #000;
  margin:0; padding:.25em;
}
#panel-one ul { margin:.25em; padding:0; }
#panel-one a { font-weight:bold; color:#000; }

#content-panel { padding:4px; }

/**
 * Main Content
 */
#errorMessages { background-color:#fcc; padding:4px; border-radius:10px; }
#errorMessages h2 { }

.breadcrumbs ul { list-style-type:none; }
.breadcrumbs li { display:inline; }
.breadcrumbs li a { margin-right:8px; font-size:.75em; color:#999; }


.pageNavigation ul { list-style-type:none; }
.pageNavigation li { display:inline; margin-right:1em; }
.pageNavigation .current { border:2px solid green; }

.tabBar { list-style-type:none; margin:8px; border-bottom:4px solid #036; }
.tabBar li { display:inline; margin:.5em; padding:4px 8px 0 8px; border-radius:4px; }
.tabBar li.current { background-color:#036; }
.tabBar li.current a { color:white; }

/* Tag Styles */
.tags   { font-size:20px; }
.tags a { font-size:20px; }
.tags .p90 a { font-size:110%; color:black; font-weight:bold; }
.tags .p70 a { font-size:100%; color:black;   }
.tags .p50 a { font-size:90%;  color:#150517; }
.tags .p30 a { font-size:75%;  color:#2b1b17; }
.tags .p10 a { font-size:60%;  color:#302226; }
.tags .p0  a { font-size:50%;  color:#3b3131; }
.tags a.current { color:#ee7d22; }

tr.current { background-color:#ccc; }
a.current { font-weight:bold; }

tr.carryover { background-color:#fc6; }

.votingRecord .yes     { padding-left:28px; background:url('images/check.gif') top left no-repeat; }
.votingRecord .no      { padding-left:28px; background:url('images/x.gif')     top left no-repeat; }
.votingRecord .absent  { padding-left:28px; background:url('images/blank.gif') top left no-repeat; }
.votingRecord .abstain { padding-left:28px; background:url('images/dash.gif')  top left no-repeat; }
